



























import { Vue, Component } from 'vue-property-decorator';
import { ServiceDatetime, ServiceRegex, ServiceRole, ServiceTypeof, ServiceFormConfig } from '@/../helpers';
import { IComponent } from '@/../models';
import { EFormType } from '@/../enums';

// Component
import FormRelationSelect from '@/../components/form/form_relational_select.vue';

interface ISelectOptionItemData {
    build: IComponent.ISelectionBase[];
    floor: IComponent.ISelectionClild[];
    allFloor: IComponent.ISelectionClild[];
}

interface ISelectOptionItem {
    building: { options: any[] };
    floor: {
        options: any[];
        allOptions: any[];
    };
}

// form
interface IFormItem {}

interface IFormData {
    building: IComponent.ISelectionBase;
    floor: any[];
}

interface IFormDataOptions {
    building: { options: any[] };
    floor: {
        options: any[];
        allOptions: any[];
    };
}

interface IFormDataDisabled {}

interface IFormRelationSelectConfigs {
    building: IComponent.IFormRelationSelectConfig;
    floor: IComponent.IFormRelationSelectConfig;
}

interface IFormButtonDisabled {
    building: boolean;
    floor: boolean;
}

@Component({
    components: { FormRelationSelect },
})
export class FormRelationalSelectSingle extends Vue {
    private selectOptionItemData: ISelectOptionItemData = {
        build: [],
        floor: [],
        allFloor: [],
    };

    private selectOptionItem: ISelectOptionItem = {
        building: { options: [] },
        floor: {
            options: [],
            allOptions: [],
        },
    };

    private formItem: IFormItem = {};

    private formDataOriginal: IFormData = {
        building: null,
        floor: [],
    };
    private formData: IFormData = { ...this.formDataOriginal };

    private formDataDisabledOriginal: IFormDataDisabled = {};
    private formDataDisabled: IFormDataDisabled = { ...this.formDataDisabledOriginal };

    private formConfigOriginal: IFormRelationSelectConfigs = {
        building: JSON.parse(JSON.stringify(ServiceFormConfig.getFormRelationSelectConfig())),
        floor: JSON.parse(JSON.stringify(ServiceFormConfig.getFormRelationSelectConfig())),
    };
    private formConfig: IFormRelationSelectConfigs = { ...this.formConfigOriginal };

    private formButtonDisabled: IFormButtonDisabled = {
        building: true,
        floor: true,
    };

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {
        await this.initSelectOption();
    }
    private async mounted(): Promise<void> {
        await this.initFormConfig();
        this.initFormData();
        this.initFormDataDisabled();
    }
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    //////////////////// Init Data ////////////////////

    private initFormConfig(): void {
        // building
        this.formConfig.building.id = 'building';
        this.formConfig.building.label = this.$i18n.Component_Form_Relational_Select_Building;
        this.formConfig.building.placeholder = this.$i18n.Component_Form_Relational_Select_Building;
        this.formConfig.building.formType = EFormType.single;
        this.formConfig.building.required = true;
        this.formConfig.building.cols = 6;

        // floor
        this.formConfig.floor.id = 'floor';
        this.formConfig.floor.label = this.$i18n.Component_Form_Relational_Select_Floor;
        this.formConfig.floor.placeholder = this.$i18n.Component_Form_Relational_Select_Floor;
        this.formConfig.floor.formType = EFormType.single;
        this.formConfig.floor.required = true;
        this.formConfig.floor.cols = 6;
    }

    private initFormData(): void {
        // let result: any = await this.$server.R('/setting/frs').catch((e: any) => ServiceResponseFilter.catchError(this, e));
        let result: any = {
            imageOrignalSrc: 'file/file-person-staff/5eb9013fd0ce4928508a9727?timestamp=1589182785332',
            account: 'sci',
            name: 'sci',

            role: 'Actor',
            company: { objectId: 'companyId4', name: 'KKBox' },
            build: { objectId: 'buildingId1', name: 'Building 101' },
            floor: { parentId: 'buildingId1', name: '101 / KKBox Floor 77F', objectId: 'floorId3' },

            email: 'sci@sci.sci',
            phone: '',
            remark: '',

            protocol: 'http',
            ip: '127.0.0.1',
            port: 8080,
            ssl: false,

            year: new Date(),
            month: new Date(),
            week: new Date(),
            date: new Date(),
            datetime: new Date(),
            time: new Date(),
        };

        if (result && ServiceTypeof.isObject(result)) {
            this.formDataSet(result);
        }
    }

    private initFormDataDisabled(): void {
        this.formDataDisabled = JSON.parse(JSON.stringify(this.formDataDisabledOriginal));
    }

    //////////////////// Init Select Option  ////////////////////
    private async initSelectOption(): Promise<void> {
        this.initSelectionBuilding();
        this.initSelectionFloor();
    }

    private async initSelectionBuilding(): Promise<void> {
        this.selectOptionItemData.build = [
            { objectId: 'buildingId1', name: '101' },
            { objectId: 'buildingId2', name: 'World Trade Center' },
        ];
        this.selectOptionItem.building.options = this.selectOptionItemData.build;
    }

    private async initSelectionFloor(): Promise<void> {
        this.selectOptionItemData.floor = [
            {
                objectId: 'floorId0',
                name: '101 / Yahoo Floor 54F',
                parentId: 'buildingId1',
            },
            {
                objectId: 'floorId1',
                name: '101 / Yahoo Floor 55F',
                parentId: 'buildingId1',
            },
            {
                objectId: 'floorId2',
                name: '101 / Yahoo Floor 56F',
                parentId: 'buildingId1',
            },
            {
                objectId: 'floorId3',
                name: '101 / KKBox Floor 77F',
                parentId: 'buildingId1',
            },
            {
                objectId: 'floorId4',
                name: '101 / KKBox Floor 78F',
                parentId: 'buildingId1',
            },

            {
                objectId: 'floorId5',
                name: '101 / Google Floor 60F',
                parentId: 'buildingId1',
            },
            {
                objectId: 'floorId6',
                name: '101 / Google Floor 61F',
                parentId: 'buildingId1',
            },
            {
                objectId: 'floorId7',
                name: 'World Trade Center / Apple Floor 2F',
                parentId: 'buildingId2',
            },

            {
                objectId: 'floorId8',
                name: 'World Trade Center / Apple Floor 3F',
                parentId: 'buildingId2',
            },
            {
                objectId: 'floorId9',
                name: 'World Trade Center / Apple Floor 4F',
                parentId: 'buildingId2',
            },
            {
                objectId: 'floorId10',
                name: 'World Trade Center / Apple Floor 5F',
                parentId: 'buildingId2',
            },
            {
                objectId: 'floorId11',
                name: 'World Trade Center / Apple Floor 6F',
                parentId: 'buildingId2',
            },

            {
                objectId: 'floorId12',
                name: 'World Trade Center / KKBox Floor 10F',
                parentId: 'buildingId2',
            },
        ];
        this.selectOptionItem.floor.options = this.selectOptionItemData.floor;
        this.selectOptionItem.floor.allOptions = this.selectOptionItemData.floor;
    }

    //////////////////// Form Show ////////////////////
    private formSaveDisable(): boolean {
        return Object.values(this.formButtonDisabled).some((x) => x === true);
    }

    //////////////////// Form Set Data ////////////////////

    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
    }

    private formConfigValues(value: any, disabledBtuuon: boolean, id: string): void {
        Object.keys(this.formData).forEach((key) => {
            if (id === key) this.formData[id] = value;
        });

        Object.keys(this.formButtonDisabled).forEach((key) => {
            if (id === key) this.formButtonDisabled[id] = disabledBtuuon;
        });
    }

    private formDisabledValues(value: boolean, id: string): void {
        Object.keys(this.formDataDisabled).forEach((key) => {
            if (id === key) this.formDataDisabled[id] = value;
        });
    }

    private formDataSet(data: any): void {
        this.formData.building = data.build;
        this.formData.floor = data.floor;
    }

    //////////////////// Modify API (save, delete, test)  ////////////////////

    //////////////////// Resolve text ////////////////////

    //////////////////// Modal ////////////////////
}
export default FormRelationalSelectSingle;
Vue.component('form-relational-select-single', FormRelationalSelectSingle);
