


































































































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

import { EFormType } from '@/../enums';
import { ServiceRegex, ServiceTypeof } from '@/../helpers';
import { IComponent } from '@/../models';

// vue-multiselect
import Multiselect from 'vue-multiselect';
import 'vue-multiselect/dist/vue-multiselect.min.css';

interface ISelectOptionItemParent {
    options: any[];
}

interface ISelectOptionItemChild {
    options: any[];
    allOptions: any[];
}

@Component({
    components: { Multiselect },
})
export class FormRelationSelect extends Vue {
    //////////////////// Prop ////////////////////
    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => {},
    })
    private formConfigParent: IComponent.IFormRelationSelect;

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => {},
    })
    private formConfigChild: IComponent.IFormRelationSelect;

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => null,
    })
    private formData: any;

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => null,
    })
    private formDataOptions: any;

    //////////////////// Variable ////////////////////
    private formItemParent: IComponent.IFormRelationSelect = {
        // 必傳參數
        id: '',

        // 可傳可不傳參數，不傳為預設值
        label: '',
        placeholder: '',
        formType: null,
        required: null,
        cols: 12,

        // 元件內部使用
        model: null,
        disabled: null,
        isRequired: null,
        disabledButton: true,
    };

    private formItemChild: IComponent.IFormRelationSelect = {
        // 必傳參數
        id: '',

        // 可傳可不傳參數，不傳為預設值
        label: '',
        placeholder: '',
        formType: null,
        required: null,
        cols: 12,

        // 元件內部使用
        model: null,
        disabled: null,
        isRequired: null,
        disabledButton: true,
    };

    private selectOptionItemParent: ISelectOptionItemParent = {
        options: [],
    };

    private selectOptionItemChild: ISelectOptionItemChild = {
        options: [],
        allOptions: [],
    };

    private eFormType = EFormType;

    //////////////////// Vue Life ////////////////////
    private created(): void {}

    private mounted(): void {}

    //////////////////// Watch ////////////////////

    @Watch('formConfigParent', { immediate: true, deep: true })
    private formConfigParentChanged(newVal: any, oldVal: any): void {
        this.formItemParent = this.initFormItem(this.formConfigParent, this.formItemParent);
        this.initFormOptions(this.formDataOptions, this.formConfigParent, this.selectOptionItemParent);
    }

    @Watch('formConfigChild', { immediate: true, deep: true })
    private formConfigChildChanged(newVal: any, oldVal: any): void {
        this.formItemChild = this.initFormItem(this.formConfigChild, this.formItemChild);
        this.initFormOptions(this.formDataOptions, this.formConfigChild, this.selectOptionItemChild);
    }

    @Watch('formDataOptions', { immediate: true, deep: true })
    private formDataOptionsChanged(newVal: any, oldVal: any): void {
        this.initFormOptions(this.formDataOptions, this.formConfigParent, this.selectOptionItemParent);
        this.initFormOptions(this.formDataOptions, this.formConfigChild, this.selectOptionItemChild);
    }

    @Watch('formData', { immediate: true, deep: true })
    private formDataChanged(newVal: any, oldVal: any): void {
        this.initFormData(this.formData, this.formItemParent);
        this.initFormData(this.formData, this.formItemChild);
        this.formEmitValueParent();
        this.formEmitValueChild();
    }

    //////////////////// Init ////////////////////
    private initFormItem(formConfig: any, formItem: any): IComponent.IFormItem {
        let tempFromConfig: IComponent.IFormItem = JSON.parse(JSON.stringify(formItem));

        // id 必傳，deault 為 'id'
        if (formConfig.id !== undefined && formConfig.id !== null && ServiceTypeof.isString(formConfig.id)) {
            tempFromConfig.id = formConfig.id;
        } else {
            console.error('iError !!! Id Not Found. Id is required, please check your id');
        }

        // cols 非必傳，deault 為 12，出現錯誤跳 console.log 提示
        let colErrorTitle: string = '';
        let colErrorMessage: string = 'Cols must be a positive integer between 1-12';

        if (formConfig.cols && ServiceTypeof.isNumber(formConfig.cols)) {
            if (formConfig.cols < 1) {
                // 如果傳 小於 1，deault 為 12
                tempFromConfig.cols = 12;
                colErrorTitle = `Error !!! Your cols are ${formConfig.cols} !!! Cols Cannot Less than 1`;
                console.error(colErrorTitle);
            } else if (formConfig.cols > 12) {
                // 如果傳 大於 12，deault 為 12
                tempFromConfig.cols = 12;
                colErrorTitle = `Error !!! Your cols are ${formConfig.cols} !!! Cols Cannot Exceed 12`;
                console.error(colErrorTitle);
            } else if (ServiceRegex.isFloat(formConfig.cols.toString())) {
                // 如果傳 小數，deault 為 12
                tempFromConfig.cols = 12;
                colErrorTitle = `Error !!! Your cols are ${formConfig.cols} !!! Cols Must be an Integer`;
                console.error(colErrorTitle);
            } else if (formConfig.cols >= 1 && formConfig.cols <= 12 && ServiceRegex.isInt(formConfig.cols.toString())) {
                tempFromConfig.cols = formConfig.cols;
            } else {
                tempFromConfig.cols = 12;
            }
        } else {
            tempFromConfig.cols = 12;
            colErrorTitle = `Error !!! Your cols are ${formConfig.cols} !!! Cols Must be an Integer`;
            console.error(colErrorTitle);
        }

        // formType 非必傳，deault 為 EFormType.text
        tempFromConfig.formType = formConfig.formType ? formConfig.formType : EFormType.single;

        // required 非必傳，deault 為 true
        tempFromConfig.required = formConfig.required === true ? true : false;

        // label, placeholder 非必傳，deault 為 ''
        tempFromConfig.label = ServiceTypeof.isString(formConfig.label) ? formConfig.label : '';

        // tempFromConfig.label = formConfig.label ? formConfig.label : '';
        tempFromConfig.placeholder = formConfig.placeholder ? formConfig.placeholder : '';

        formItem = JSON.parse(JSON.stringify(tempFromConfig));
        return formItem;
    }

    private initFormData(formData: any, formItem: any): void {
        if (ServiceTypeof.isObject(formItem) && ServiceTypeof.isObject(formData)) {
            for (const key in formData) {
                if (formData.hasOwnProperty(key)) {
                    if (formItem.id === key) {
                        switch (formItem.formType) {
                            case EFormType.single:
                                formItem.model = formData[key] ? formData[key] : null;
                                break;
                            case EFormType.multiple:
                                if (ServiceTypeof.isArray(formData[key])) {
                                    formItem.model = formData[key] ? formData[key] : [];
                                } else {
                                    formItem.model = [];
                                }
                                break;
                        }
                    }
                }
            }
        }
    }

    private initFormDisabled(formDataDisabled: any, formItem: any): void {
        if (ServiceTypeof.isObject(formItem) && ServiceTypeof.isObject(formDataDisabled)) {
            for (const key in formDataDisabled) {
                if (formDataDisabled.hasOwnProperty(key)) {
                    if (formItem.id === key) {
                        formItem.disabled = formDataDisabled[key];
                    }
                }
            }
        }
    }

    private initFormOptions(formDataOptions: any, formConfig: any, selectOptionItem: any): any {
        if (ServiceTypeof.isObject(formDataOptions) && ServiceTypeof.isObject(formDataOptions)) {
            for (const key in formDataOptions) {
                if (formDataOptions.hasOwnProperty(key)) {
                    if (formConfig.id === key) {
                        if (selectOptionItem.options) {
                            selectOptionItem.options = formDataOptions[key].options ? formDataOptions[key].options : [];
                        }
                        if (selectOptionItem.allOptions) {
                            selectOptionItem.allOptions = formDataOptions[key].options ? formDataOptions[key].options : [];
                        }
                    }
                }
            }
        }

        return selectOptionItem;
    }

    //////////////////// Form Clear  ////////////////////

    //////////////////// Form Type  ////////////////////

    private formTypeSelectSingle(type: string): boolean {
        return type === EFormType.single;
    }

    private formTypeSelectMultiple(type: string): boolean {
        return type === EFormType.multiple;
    }

    //////////////////// Event  ////////////////////
    private formSelectSingleInputEvent(): void {
        if (this.formConfigChild.formType === EFormType.single) {
            this.formItemChild.model = null;
        } else {
            this.formItemChild.model = [];
        }

        this.formEmitValueChild();
        const data: IComponent.IFormRelationSelect = this.formItemParent;
        let disabled: boolean = null;
        let tempOprions: any[] = [];
        if (data.required === true) {
            disabled = data.model ? false : true;
        } else {
            disabled = false;
        }

        if (data.model) {
            tempOprions = (this.selectOptionItemChild.allOptions || []).filter((item) => item.parentId === data.model.objectId);
        } else {
            tempOprions = this.selectOptionItemChild.allOptions;
        }

        this.selectOptionItemChild.options = tempOprions;
        this.$emit('input', data.model, disabled, data.id);
    }

    private formSelectMultipleInputEvent(): void {
        const data: IComponent.IFormRelationSelect = this.formItemChild;
        let disabled: boolean = null;

        if (data.formType === EFormType.multiple) {
            if (data.required === true) {
                disabled = data.model.length > 0 ? false : true;
            } else {
                disabled = false;
            }
        }
        if (data.formType === EFormType.single) {
            if (data.required === true) {
                disabled = data.model ? false : true;
            } else {
                disabled = false;
            }
        }

        this.$emit('input', data.model, disabled, data.id);
    }

    private formEmitValueParent(): void {
        const data: IComponent.IFormRelationSelect = this.formItemParent;

        let disabled: boolean = null;
        let tempOprions: any[] = [];
        if (data.required === true) {
            disabled = data.model ? false : true;
        } else {
            disabled = false;
        }

        if (data.model) {
            tempOprions = (this.selectOptionItemChild.allOptions || []).filter((item) => item.parentId === data.model.objectId);
        } else {
            tempOprions = this.selectOptionItemChild.allOptions;
        }

        if (data.required === true) {
            disabled = data.model ? false : true;
        } else {
            disabled = false;
        }

        this.selectOptionItemChild.options = tempOprions;
        this.$emit('input', data.model, disabled, data.id);
    }

    private formEmitValueChild(): void {
        const data: IComponent.IFormRelationSelect = this.formItemChild;
        let disabled: boolean = null;

        if (data.formType === EFormType.single) {
            if (data.required === true) {
                disabled = data.model ? false : true;
            } else {
                disabled = false;
            }
        } else {
            if (data.required === true) {
                disabled = data.model.length > 0 ? false : true;
            } else {
                disabled = false;
            }
        }

        this.$emit('input', data.model, disabled, data.id);
    }

    //////////////////// Value Check ////////////////////

    //////////////////// State Value ////////////////////
}

export default FormRelationSelect;
Vue.component('form-relation-select', FormRelationSelect);
